import React, { Component } from "react";
import { Container } from "reactstrap";



export default class Main extends Component {
  render() {
    return (
      <div className="container-fluid text-center mt-5">
        <img src="https://www.teahub.io/photos/full/220-2205714_get-the-latest-movies-data-src-kodi-tv.jpg" className="img-fluid main-img card-img " alt="..."/>
        <div className="card-img-overlay">
          <div>
            <h5 className="card-title">KEYFLIX</h5>
            <a href="https://ertugrulsarihan.github.io/Keyflixlogin/" className="btn login-btn ">Login</a>
          </div>
          <div className="container-text">
            <Container className="mini-cont">
           
            <p className="card-text mt-5">Aşağıdaki kutuya E-postanı girerek başla!</p>
            </Container>
            <Container className="" style={{width:"320px"}}>
            <div className="input-group mt-3">
              <input
                type="text"
                className="form-control"
                placeholder="e-posta adresi"
                
               
              />
            
                <button className="btn btn-email" >
                  Katıl!
                </button>
              </div>
            
            </Container>
          </div>
        </div>
      </div>
    );
  }
}
