import React, { Component } from "react";
import Tablet from "./Components/tablet.mp4"


export default class Caro extends Component {
  render() {
    return (
     <div className="container  ">
       <video className="tablet-container img-fluid mt-5 pb-5" autoPlay loop muted>
     <source className="tablet-container img-fluid mt-5" src={Tablet}  />
      </video>

       <h4 className="mb-5  second-text">İstediğin yerden izle.</h4>
       <p2 >İndir ve istediğin yerden çevrimdışı izle.</p2>

     </div>
    );
  }
}
