import React, { Component } from "react";
import { Button, Accordion, Card } from "react-bootstrap";
import { Container } from "reactstrap";

export default class Footer extends Component {
  render() {
    return (
      <Container className="mt-5 mb-5  ">
        <Accordion defaultActiveKey="0">
          <Card className="bg-dark">
            <Card.Header>
              <Accordion.Toggle
                className="Toggle"
                as={Button}
                variant="link"
                eventKey="1"
              >
                   <span >+</span>
                <span >Keyflix nedir?</span>
                
               
               
               
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="1">
              <Card.Body className="card-body bg-secondary">
                Keyflix; internet bağlantılı binlerce cihazda ödüllü diziler,
                filmler, animeler, belgeseller ve daha fazlasını içeren geniş
                bir arşiv sunan bir yayın hizmetidir.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card className="mt-3 bg-dark">
            <Card.Header>
              <Accordion.Toggle
                className="Toggle"
                as={Button}
                variant="link"
                eventKey="2"
              >
                   <span >+</span>
                <span >Nasıl iptal edebilirim?</span>
               
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="2">
              <Card.Body className=" card-body bg-secondary">
                Keyflix esnektir. Sinir bozucu hiçbir sözleşme ve taahhüt
                yoktur. Hesabınızı çevrimiçi olarak iki tıklamayla kolayca iptal
                edebilirsiniz. İptal ücreti yoktur - hesabınızı istediğiniz
                zaman başlatın veya durdurun.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card className="mt-3 bg-dark">
            <Card.Header>
              <Accordion.Toggle
                className="Toggle"
                as={Button}
                variant="link"
                eventKey="3"
              >
                   <span >+</span>
                <span >Keyflix'te ne izleyebilirim?</span>
                
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="3">
              <Card.Body className=" card-body bg-secondary">
                Keyflix, uzun metrajlı filmler, belgeseller, diziler ve
                programlar, anime, ödüllü Netflix orijinal içerikleri ve daha
                fazlasından oluşan kapsamlı bir kütüphaneye sahiptir.
                İstediğiniz her zaman, istediğiniz kadar çok şey izleyin.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </Container>
    );
  }
}
