import React, { Component } from "react";
import { Container } from "reactstrap";
import Keyflix from "../Components/keyflix.mp4"

export default class Line extends Component {
  render() {
    return (
      <Container className="mt-1">
        <Container>
          <div className="tv-img pb-5 ">
            <video className="tv-img img-fluid" autoPlay loop muted>
              <source className="tv-img img-fluid" src={Keyflix} />
            </video>
          </div>
          <div className="img-text1 pb-5">
            <p1>Televizyonunuzda izleyebilirsiniz.</p1>
            <div>
              <p2>
                Xbox,Playstation,Apple TV,Bluray oynatıclar ve daha fazlasında
                seyredin.
              </p2>
            </div>
          </div>
        </Container>
      </Container>
    );
  }
}
